<template>
  <div>
    <form class="form-signup" v-on:submit="onSubmitForm">
        <div class="cardid">
          <span>
            {{card.type}}-{{card.id}}
          </span>
          <button class="btn-save" type="submit">
            Save
          </button>
        </div>
        <div>
            <textarea class="cardtitle" :class="card.type" v-model="card.title">
            </textarea>
        </div>
        <h2 class="form-signin-heading">Register</h2>
        <label for="cardName" class="sr-only">Title</label>
        <textarea id="cardName" v-model="card.title" placeholder="input card title"></textarea>
        <label for="cardDescription" class="sr-only">Description</label>
        <textarea id="cardDescription" v-model="card.description" placeholder="input card description"></textarea>

        <h1>Hello! C-{{ $route.params.id }}</h1>
        <h2>{{card}}</h2>
    </form>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'Card',
  props: ['id'],
  data () {
    return {
      card: {}
    }
  },
  beforeRouteEnter (to, from, next) {
    axios.get('/api/get-card', {
      params: {
        id : to.params.id
      }
    })
    .then((response) => {
      next(vm => {
        vm.card = response.data
      })
    })
  },
  methods:{
    onSubmitForm: function(event)
      {
        this.$http.post('api/update-card', this.card)
        .then(function (response) {
            this.$router.push('/board')
        })
      }
  }
}
</script>

<style scoped>
  h1, h2 {
    font-weight: normal;
  }

  .cardid {
    align-items: center;
    justify-content: space-between;
    color: white;
    background: #444444;
    text-align: left;
    height: 1cm;
    font-size: 0.6cm;
    margin: 0.1cm;

    .btn-save{
      border: 7px solid white;
      color: white;
      text-align: center;
      text-decoration: none;
      display: inline-block;
      border-radius: 4px;
      float: right;
      font-size: 0.4cm;

    }
  }



  .cardtitle{
    font-family: "Proxima Nova","Lucida Sans Unicode","Lucida Grande",sans-serif;
    font-size: 1.6rem;
    color: white;
    padding: 0.5em;
    line-height: 1em;
    height: 100%;
    width: 100%;
    box-sizing: border-box;
  }

  .DEFECT {
      background : red;
  }

  .STORY{
      background: green;
  }



</style>



